import { get,post } from '../api/api'
export const puHttp = {
  //mock假数据
  queryHomeList(params) {
    return get('mob/query/list', params)
  },
  //绩效考核任务—考核任务查询
  queryList(params){
    return get('/performance/tasklist?timerp='+Date.now(),params)
  },
  //绩效考核任务—详情
  tackDe(params){
    return get('/performance/taskdetail?timp='+Date.now(),params)
  },
  //绩效考核任务—删除
  tackSc(params){
    return post('/performance/deletetask',params)
  },
  //绩效考核任务—新增
  addObj(params){
    return post('/performance/addtask',params)
  },
  //绩效考核任务—考核部门
  department(params){
    return get('/common/department',params)
  },
  //绩效考核任务—考核数据
  kData(params){
    return get('/common/data',params)
  },
  //绩效考核任务—修改
    taskUp(params){
      return post('/performance/updatetask',params)
    },
  //考核结果查询—月度绩效查询列表页
  resultList(params){
    return get('/performance/resultlist',params)
  },
  //考核绩效报表—绩效报表
  reportList(params){
    return get('/performance/statementlist',params)
  },
  //考核绩效报表—绩效报表详情
  reportListXq(params){
    return get('/performance/statementdetaillist?tim='+Date.now(),params)
  },

  //考核结果查询—下载
  resultDownload(params){
    return get("/performance/downloadassess",params)
  },
  //考核绩效报表—下载
   reportDownload(params){
    return get("/performance/downloadstatement",params)
  }
};



