<template>
  <div class="left-bar fl">
    <header>绩效考核大数据分析</header>
    <ul class="menu">
      <router-link class="item" tag="li" to="/home/home1">
        <i class="iconfont icon-lingdaohuodong"></i>领导驾驶舱
      </router-link>
      <!--<router-link class="item" tag="li" to="/home/home2">-->
        <!--<i class="iconfont icon-evaluation-o"></i>绩效考核分析-->
      <!--</router-link>-->
      <router-link class="item" tag="li" to="/home/JbAnalysis">
        <i class="iconfont icon-renwu"></i>绩效工作分析
      </router-link>
      <router-link class="item" tag="li" to="/home/AssessmentTask" @click.native="flushCom">
        <i class="iconfont icon-shezhikaohefangan"></i>绩效考核任务
      </router-link>
      <router-link class="item" tag="li" to="/home/ResultQuery" @click.native="flushCom">
        <i class="iconfont icon-chaxun"></i>绩效结果查询
      </router-link>
      <router-link class="item" tag="li" to="/home/AssessmentReport" @click.native="flushCom">
        <i class="iconfont icon-baobiao"></i>绩效考核报表
      </router-link>
    </ul>
  </div>
</template>
<script>
  export default {
    name: 'app',
    methods:{
      flushCom(){
        if(global.callBackFn){
          global.callBackFn();
        }
      }
    }
  }
</script>
<style lang="scss" scoped>
  /*i{*/
    /*color: #00bdff;*/
  /*}*/
    .left-bar{
      width: 200px;
      /*height: 1000px;*/
      background: #fff;
      min-height:calc(100vh - 80px);
      header{
        width: 100%;
        height: 60px;
        line-height: 60px;
        text-align: center;
        font-size: 18px;
        font-weight: bold;
        border-bottom: 1px solid #eff3fa;
      }
      li{
        line-height: 50px;
        padding: 0 20px;
        cursor: pointer;
        border-bottom: 1px solid #eff3fa;
        font-size: 16px;
        font-weight: 500;
        i{
          margin-right: 12px;
          color: #3463ba;
          font-size: 20px;
        }
        &.active{
          color:#fff !important;
          background-color: #5798f7;
          transition: all .3s;
          border-bottom: 1px solid #5798f7;
          i{
            color: #fff;
          }
        }
        &:hover{
          color:#fff;
          background-color: #5798f7;
          transition: all .3s;
          border-bottom: 1px solid #5798f7;
          i{
            color: #fff;
          }
        }
      }
    }
</style>



