<template>
  <div class="home5-con">
    <headerIn5 @query="query"></headerIn5>
    <contentIn5 :weObject='weObject'></contentIn5>
  </div>
</template>

<script type="es6">
  import headerIn5 from "./headerIn5";
  import contentIn5 from "./contentIn5";
  export default {
    name: 'home',
    data(){
      return {
        weObject:{
          quName:'',
          quType:'',
          startDate:'',
          endDate:'',
        }
      }
    },
    methods:{
      query(data) {
//        if (data.input=='' && data.value == ""&& data.Date1 == "" && data.Date2 == ""){
//          this.openObject("搜索内容不能为空");
//          return
//        }
        this.weObject.quName=data.input
        this.weObject.quType=data.value
        if (data.Date1!=''){
          this.weObject.startDate=this.dateObj(data.Date1)
        }else {
          this.weObject.startDate=""
        }
        if (data.Date2!=''){
          this.weObject.endDate=this.dateObj(data.Date2)
        }else {
          this.weObject.endDate=""
        }
      }
    },
    components:{
      headerIn5,
      contentIn5
    }
  }
</script>

<style lang="scss" scoped>
</style>



