<template>
  <div class="con">
    <div class="hea_tit">
      <div class="ball">
        <span></span>
      </div>
      <span v-if="xqType==1"><router-link :to="{path:'/home/AssessmentReport/monthTab'}">{{year}}年-{{nameId}}月度绩效考核数据报表</router-link></span>
      <span v-if="xqType==2"><router-link :to="{path:'/home/AssessmentReport/seasonTab'}">{{year}}年-{{nameId}}季度绩效考核数据报表</router-link></span>
      <span v-if="xqType==3"><router-link :to="{path:'/home/AssessmentReport/yearTab'}">{{nameId}}年度绩效考核数据报表</router-link></span>
      <button class="btn2" @click="download"><i class="el-icon-download"></i><span>下载</span></button>
    </div>
    <div class="table-warp">
      <table>
        <tr class="thead">
          <td>序号</td>
          <td>上传部门</td>
          <td>数据分类</td>
          <td>上传次数</td>
          <td>信息事项数量</td>
          <td>信息数据数量</td>
          <td>有效信息项</td>
          <td>有效信息数量</td>
        </tr>
        <tr v-if="list.length > 0" v-for="(item,idx) in list">
          <td>{{idx+1}}</td>
          <td>{{item.UPLOAD_DEPARTMENT}}</td>
          <td>{{item.DATA_CLASSIFY}}</td>
          <td>{{item.UPLOAD_FREQUENCY}}</td>
          <td>{{item.MATTER_COUNT}}</td>
          <td>{{item.DATA_COUNT}}</td>
          <td>{{item.VALID_MATTER_COUNT}}</td>
          <td>{{item.VALID_DATA_COUNT}}</td>
        </tr>
        <tr v-if="list.length === 0" class="noDataTr">
          <span>暂无数据</span>
        </tr>
      </table>
      <div class="pagination fr">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="10"
          layout="prev, pager, next, jumper"
          :total=this.count>
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script type="es6">
  import {puHttp} from '../../api/puHttp'
  export default {
    name: 'home',
    data(){
      return {
        currentPage:1,
        list:[],
        count:0,
        nameId:'',
        xqType:'',
        year:''
      }
    },
//    mounted() {
//      this.nameId = this.$route.query.id
//      this.year = this.$route.query.year
//      this.xqType = this.$route.query.type
//      this.queryList();
//    },
    activated(){
      this.nameId = this.$route.query.id
      this.year = this.$route.query.year
      this.xqType = this.$route.query.type
      this.queryList();
    },
    methods: {
      handleCurrentChange(val) {
        this.currentPage = val;
        this.queryList();
      },
      queryList(){
        const parData={}
        if (this.xqType==1){
          parData.page=this.currentPage
          parData.rows=10
          parData.type=1
          parData.month=this.nameId
          parData.year=this.year
        }else if(this.xqType==2){
          parData.page=this.currentPage
          parData.rows=10
          parData.type=2
          parData.quarter=this.nameId
          parData.year=this.year
        }else if(this.xqType==3){
          parData.page=this.currentPage
          parData.rows=10
          parData.type=3
          parData.year=this.nameId
        }
        puHttp.reportListXq(parData).then((result)=>{
          this.list=result.data
          this.count=result.count
        })
      },
      //下载
      download(){
        const parData={}
        if(this.xqType==1){
          parData.type=1;
          parData.year=this.year
          parData.month=this.nameId
        }
        if (this.xqType==2){
          parData.type=2;
          parData.year=this.year
          parData.quarter=this.nameId
        }
        if (this.xqType==3){
          parData.type=3;
          parData.year=this.nameId
        }
        puHttp.reportDownload(parData).then((result)=>{
          let base_url = window.BASE_URL;
          if(this.xqType==1){
            location.href=base_url+'/performance/downloadstatement?type=1&year='+this.year+'&month='+this.nameId
          }
          if(this.xqType==2){
            location.href=base_url+'/performance/downloadstatement?type=2&year='+this.year+'&quarter='+this.nameId
          }
          if(this.xqType==3){
            location.href=base_url+'/performance/downloadstatement?type=3&year='+this.year
          }
        }).catch(() => {
          this.openObject("服务器超时请稍后重试")
        });
      }
    },
  }
</script>

<style lang="scss" scoped>
  .con{
    width: 100%;
    /*height: 795px;*/
    min-height: calc(100vh - 120px);
    background-color: #fff;
  }
</style>



