<template>
  <div>
    <div class="hea_con">
      <ul>
        <li class="el-input__Date">
          <span>任务名称</span>
          <span><el-input v-model="input" placeholder="请输入内容"></el-input></span>
        </li>
        <li>
          <el-button type="primary" icon="el-icon-search" @click="queryObj"><span>查询</span></el-button>
          <button class="btn_cz" @click="reset"><i class="iconfont icon-xuanzhuanLeft"></i><span>重置</span></button>
        </li>
      </ul>
    </div>
    <div class="con">
      <div class="hea_tit">
        <div class="ball">
          <span></span>
        </div>
        <span>绩效报表</span>
      </div>
      <div class="table-warp">
        <table>
          <tr class="thead">
            <td>序号</td>
            <td>报告名称</td>
            <td>操作</td>
          </tr>
          <tr v-if="list.length > 0" v-for="(item,idx) in list">
            <td>{{idx+1}}</td>
            <td>{{item.STATEMENTNAME}}</td>
            <td>
              <router-link :to="{path:'/home/AssessmentReport/details', query:{id:item.MONTH,year:item.YEAR,type:1
}}"><button><i class="iconfont icon-yanjing"></i><span>查看</span></button></router-link>
              <button @click="download(item)" class="xz"><i class="iconfont el-icon-download"></i><span>下载</span></button>
            </td>
          </tr>
          <tr v-if="list.length === 0" class="noDataTr">
            <span>暂无数据</span>
          </tr>
        </table>
        <div class="pagination fr">
          <el-pagination
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="10"
            layout="prev, pager, next, jumper"
            :total=this.count>
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="es6">
  import {puHttp} from '../../api/puHttp'
  export default {
    name: 'home',
    data(){
      return {
        currentPage:1,
        list:[],
        count:0,
        input:''
      }
    },
//    mounted() {
//      this.queryList();
//    },
    activated:function () {
      
      this.currentPage = 1;
      this.queryList();
      
      global.callBackFn = this.reset;
      this.reset();
    },
    methods: {
      handleCurrentChange(val) {
        this.currentPage = val;
        this.queryList();
      },
      queryList() {
        const parData={}
        if (this.input==''){
          parData.page=this.currentPage,
          parData.rows=10
          parData.type=1
        }else {
          parData.page=this.currentPage,
          parData.rows=10
          parData.type=1
          parData.statementName=this.input
        }
        puHttp.reportList(parData).then((result) => {
          this.list = result.data;
          this.count = result.count
        })
      },
      reset() {
        this.input = '';
        this.queryList()
      },
      queryObj() {
        this.queryList()
      },
      //下载
      download(downloadId){
        puHttp.reportDownload({
          type:1,
          year:downloadId.YEAR,
          month:downloadId.MONTH,
        }).then((result)=>{
          let base_url = window.BASE_URL;
          location.href=base_url+'/performance/downloadstatement?type=1&year='+downloadId.YEAR+'&month='+downloadId.MONTH
        }).catch(() => {
          this.openObject("服务器超时请稍后重试")
        });
      }
    }
  }
</script>

<style lang="scss" scoped>
  .hea_con{
    position: absolute;
    top: 160px;
    li:first-child{
      width: 350px;
      /*width:20%;*/
      .el-input{
        width:60%;
      }
    }
    li:last-child {
      /*width: 20%;*/
      margin-top: 25px !important;
    }
  }
  .el-button{
    padding: 9px 20px ;
  }
  .con{
    margin-top: 20px !important;
  }
</style>



