      <router-view name="RoutList" v-if="!$route.mate || $route.mate.keepAlive!==false"></router-view>
    </keep-alive>
    <!--<router-view name="RoutList"></router-view>-->
  </div>
</template>

<script type="es6">
  export default {
    name: 'content',
    data(){
      return {
      }
    }
  }
</script>
<style lang="scss">
  .table-warp table tr td:last-child{
    width: 280px ;
  }
</style>



