<template>
  <div class="con">
    <div class="hea_tit">
      <div class="ball">
        <span></span>
      </div>
      <span>新增考核任务</span>
    </div>
    <div class="box">
      <ul>
        <li>
          <span><i>*</i>任务名称 : </span>
          <span><el-input v-model="input" placeholder="请输入内容"></el-input></span>
        </li>
        <li>
          <span><i>*</i>任务类型 : </span>
          <span> <el-select v-model="value" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select></span>
        </li>
        <li>
          <span><i>*</i>发布日期 : </span>
          <span><el-date-picker
            v-model="Date1"
            type="date"
            :picker-options="pickerBeginDateBefore"
            placeholder="选择日期">
          </el-date-picker> </span>
        </li>
        <li>
          <span><i>*</i>截至日期 : </span>
          <span><el-date-picker
            v-model="Date2"
            type="date"
            :picker-options="pickerBeginDateAfter"
            placeholder="选择日期">
          </el-date-picker> </span>
        </li>
        <li>
          <span><i>*</i>考核部门 : </span>
          <span class="span mTop10">{{selection}}<button class="dialogBox" @click="department">选择</button>
          </span>
        </li>
        <li>
          <span><i>*</i>考核数据 : </span>
          <span class="span mTop6">{{selection2}}<button class="dialogBox" @click="obData">选择</button>
          </span>
        </li>
        <li class="ks-clear">
          <span>考核要求 : </span>
          <span>
            <el-input
              type="textarea"
              :autosize="{ minRows: 2, maxRows: 4}"
              :maxlength="120"
              placeholder="请输入内容"
              resize="none"
              v-model="textarea3">
</el-input>
          </span>
        </li>
        <li>
          <router-link to=""><button class="btn3" @click="subMit">保存</button></router-link>
          <router-link to="/home/AssessmentTask"><button class="btn4">取消</button></router-link>
        </li>
      </ul>
    </div>
    <!--部门-->
    <el-dialog
      title="考核部门"
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
      <div class="dlBox">
        <el-tree
          :data="data2"
          show-checkbox
          node-key="id"
          :default-expanded-keys="[1]"
          :default-checked-keys="selectArr"
          :props="defaultProps"
          ref="tree2"
        >
        </el-tree>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="getCheckedNodesObj">确 定</el-button>
        <el-button @click="centerDialogVisible = false">取 消</el-button>
      </span>
    </el-dialog>
    <!--数据-->
    <el-dialog
      title="考核数据"
      :visible.sync="centerDialogVisible2"
      width="30%"
      center>
      <div class="dlBox">
        <el-tree
          :data="data3"
          show-checkbox
          node-key="id"
          :default-expanded-keys="[1]"
          :default-checked-keys="select2Arr"
          :props="defaultProps"
          ref="tree"
        >
        </el-tree>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="getCheckedNodes2">确 定</el-button>
        <el-button @click="centerDialogVisible2 = false">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script type="es6" scoped>
  import {puHttp} from '../../api/puHttp'
  export default {
    name: 'home',
    data(){
      return {
        centerDialogVisible: false,
        centerDialogVisible2: false,
        options: [{
          value:1,
          label: '周考核任务'
        }, {
          value:2,
          label: '月考核任务'
        }, {
          value:3,
          label: '季考核任务'
        }, {
          value:4,
          label: '年考核任务'
        }, {
          value:5,
          label: '动态考核任务'
        }],
        value:'',
        Date1:'',
        Date2: '',
        input:'',
        textarea3:'',
        selection:'',
        selection2:'',
        selectArr:[],
        select2Arr:[],
        data2: [{
          id: 1,
          label: '泸州市',
          children: []
        }],
        data3: [],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        pickerBeginDateBefore:{
          disabledDate: (time) => {
            if (this.Date2==='') {
              return time.getTime() < Date.now() - 8.64e7
            } else {
              return time.getTime() > this.Date2 || time.getTime() < Date.now() - 8.64e7
            }
          }
        },
        pickerBeginDateAfter:{
          disabledDate: (time) => {
            if (this.Date1==='') {
              return time.getTime() < Date.now() - 8.64e7
            } else {
              return time.getTime() < this.Date1
            }
          }
        }
      }
    },
    activated(){
      this.value=''
      this.Date1='',
      this.Date2= '',
      this.input='',
      this.textarea3='',
      this.selection='',
      this.selection2='',
      this.seleObject(),
      this.$refs.tree2.setCheckedKeys([]);
      this.$refs.tree.setCheckedKeys([]);
    },
    methods:{
      //部门确定
      getCheckedNodesObj(){
        const val2=this.$refs.tree2.getCheckedNodes()
        let str='';
        if(val2.length==1){
          str+=val2[0].label
        }else {
          for (let i=0;i<val2.length;i++){
            str+=(val2[i].label+'、')
          }
        }
        this.selection=str
        this.centerDialogVisible=false
      },
      //数据确定
      getCheckedNodes2(){
        const val=this.$refs.tree.getCheckedNodes()
        let str2='';
        if(val.length==1){
          str2+=val[0].label
        }else {
          for (let i=0;i<val.length;i++){
            str2+=(val[i].label+'、')
          }
        }
        this.selection2=str2
        this.centerDialogVisible2=false
      },
      //新增提交
      subMit(){
        if (this.input==""){
          this.openObject("任务名称不能为空");
          return
        }
        if (this.value==""){
          this.openObject("任务类型不能为空");
          return
        }
        if (this.Date1==""){
          this.openObject("发布日期不能为空");
          return
        }
        if (this.Date2==""){
          this.openObject("截至日期不能为空");
          return
        }
        if (this.selection==""){
          this.openObject("考核部门不能为空");
          return
        }
        if (this.selection2==""){
          this.openObject("考核数据不能为空");
          return
        }
        if (this.input.length>40){
          this.openObject("任务名称长度不能超过40");
          return
        }
        puHttp.addObj(JSON.stringify({
          assessName:this.input,//名称
          type:this.value,//类型
          startTime:this.dateObj(this.Date1),//开始时间
          endTime:this.dateObj(this.Date2),//结束时间
          require:this.textarea3,//要求
          department:"泸州市、市委组织部、市委宣传部、市委政法委、市委编办、市发展改革委、市经济和信息化委、市教育局、市科技知识产权局、市公安局、市民政局、市司法局、市财政局、市人力资源社会保障局、市国土资源局、市住房城乡建设局、市城乡规划管理局、市城管执法局、市交通运输局、市水务局、市农业局、市商务局、市文体广局、市卫生计生委、市环境保护局、市统计局、市安全监管局、市国资委、市金融办、市食品药品监管局、市工商局、市质监局、市国税局、市地税局、成都海关驻泸州办事处、泸州出入境检验检疫局、市档案局、人行泸州中支、市住房公积金管理中心、市中级法院、市检察院、国网泸州供电公司、兴泸水务有限公司、华润兴泸燃气有限公司",//部门
          data:this.selection2,//数据
        })).then((result)=>{
          if (result.result == 1) {
            this.$router.push("/home/AssessmentTask/list");
          }
        }) .catch((error) => {
          this.openObject("服务器连接超时请稍后再试")
        })
      },
      addList(){

      },
      seleObject(){
        if (this.data2[0].children.length==0){
          puHttp.department({
          }).then((result)=>{
            result.data.forEach(val=>{
              this.data2[0].children.push({"label":val.GOV_CNAME,"id":val.GOV_ID,})
            })
          }) .catch((error) => {
            this.openObject("服务器连接超时,请稍后再试")
          })
        };
        if (this.data3.length==0){
          puHttp.kData({
          }).then((result)=>{
            result.data.forEach(val=>{
              this.data3.push({"label":val.BOOK_NAME,"id":val.BOOK_ID,})
            })
          }) .catch((error) => {
            this.openObject("服务器连接超时,请稍后再试")
          })
        }
      },
      //部门
      department(){
        this.selectArr =this.selection.split("、")
        this.centerDialogVisible = true;
      },
      //数据
      obData(){
        this.select2Arr =this.selection2.split("、")
        this.centerDialogVisible2 = true
      }
    },
  }
</script>

<style lang="scss" scoped>
  .con{
    width: 100%;
    min-height: calc(100vh - 120px);
    /*height: 960px;*/
    margin-top: 0 !important;
    background-color: #fff;
  }
  .box{
    width: 100%;
    ul{
      width: 100%;
      margin-top: 30px;
      li{
        width: 100%;
        min-height:60px;
        line-height: 60px;
        position: relative;
        span{
          height: 100%;
          display: inline-block;
          float: left;
          font-size: 14px;
          box-sizing: border-box;
        };
        .span{
          border: 1px solid #dcdfe6;
          width: 27% !important;
          height: 40px;
          margin-left: 10px;
          padding: 0 15px;
          line-height: 40px;
          color: #606266;
          border-radius:4px;
          padding-right:65px !important;
          overflow:hidden;
          white-space:nowrap;
          text-overflow:ellipsis;
          /*margin-top:6px;*/
        }
        .mTop10{
          margin-top:10px;
        }
        .mTop6{
          margin-top: 6px;
        }
        span:first-child{
          width: 35%;
          text-align: right;
          color: #646464;
          padding-right: 10px;
          i{
            color: #e23c3c;
            position: relative;
            left: -3px;
            top: 2px;
          }
        }
        span:last-child{
          width:65%;
          text-align: left;
          color: #5b5b5b;
          padding-left:10px;
          position: relative;
          .el-input,.el-select,.el-textarea{
            width: 42% !important;
          }
        }
      }
      li:last-child{
        margin-top:120px;
        text-align: center;
      }
    }
  }
  .btn3,.btn4{
    width: 90px;
    height: 35px;
    line-height: 35px;
    text-align: center;
    font-size: 14px;
    border: 1px solid #3a81f7;
    border-radius:5px;
    position: relative;
    /*left: 50%;*/
    top: 50%;
    transform: translateY(-50%);
  }
  .btn3{
    background: #4d9cf7;
    color: #fff;
  }
  .btn4{
    background: #edf5fe;
    color: #4d9cf7;
  }
  .btn3:hover,.btn4:hover{
    text-decoration: underline;
  }
  .el-popper{
    top: 295px !important;
  }
</style>



