<template>
  <div class="con">
    <div class="hea_tit">
      <div class="ball">
        <span></span>
      </div>
      <span>数据异常详情</span>
    </div>
    <div class="box">
      <ul>
        <li>
          <span>异常信息 : </span>
          <span>{{data.MESSAGE}}</span>
        </li>
        <li>
          <span>异常原因 : </span>
          <span>{{data.REASON}}</span>
        </li>
        <li>
          <span>时间 : </span>
          <span>{{data.OCCUR_DATE}}</span>
        </li>
        <li>
          <span>内容 : </span>
          <span>{{data.CONTENT}}</span>
        </li>
        <li>
          <button class="btn3" @click="backLink">返回</button>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="es6">
  import {HomeHttp} from '../../api/homeHttp';
  export default {
    name: 'homeDet',
    data(){
      return {
        data:[]
      }
    },
//    created:function(){
//      this.initData();
//    },
    activated(){
      this.initData();
    },
    methods: {
      backLink:function () {
        this.$router.go(-1)
      },
      initData: function () {
        this.queryDet();
      },
      queryDet(){
        HomeHttp.queryHomeDet({
            id:this.$route.query.id
        }).then((result)=>{
          this.data = result.data;
        }).catch(() => {});
      }
    }
  }
</script>

<style lang="scss" scoped>
  .con{
    width: 100%;
    background-color: #fff;
    min-height: calc(100vh - 120px);
    margin-top: 0 !important;
  }
  .box{
    width: 100%;
  ul{
    width: 100%;
    margin-top: 30px;
  li{
    width: 100%;
    height: 60px;
    line-height: 60px;
    position: relative;
  span{
    height: 100%;
    display: inline-block;
    float: left;
    font-size: 14px;
    box-sizing: border-box;
  };
  span:first-child{
    width: 45%;
    text-align: right;
    color: #acacac;
    padding-right: 10px;
  }
  span:last-child{
    width:55%;
    text-align: left;
    color: #5b5b5b;
    padding-left:10px;
  }
  }
  li:last-child{
    margin-top: 30px;
  }
  }
  }
  .btn3{
    width: 90px;
    height: 35px;
    line-height: 35px;
    background: #4d9cf7;
    color: #fff;
    text-align: center;
    font-size: 14px;
    border: 1px solid #3a81f7;
    border-radius:5px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
  }
  .btn3:hover{
    text-decoration: underline;
    opacity: 0.9;
  }
</style>



