<template>
  <div class="con1">
    <div class="hea_tit">
      <div class="ball">
        <span></span>
      </div>
      <span>各单位数据上传情况</span>
    </div>
    <div class="cityUnit">
    	<div class='cityUnitName'>
    		<p>市值单位</p>
    		<ul>
	      	<li v-for='(unit,index) in units' @click='doThis(index)' :class="{active : isActiveIndex === index}">{{unit}}</li>
	      </ul>
    	</div>
      <div v-if="!data.length" class="cityUnitNodata">暂无数据</div>
      <div class='cityUnitEcharts' ref="cityUnitEcharts" id='cityUnitEcharts'>
        <!--<component :is="component"></component>-->
      </div>
    </div>
  </div>
</template>

<script type="es6">
  import {HomeHttp} from '../../api/homeHttp';
  export default {
    name: 'home',
    data(){
      return {
      	isActiveIndex: 0,
        units:[
        	'公安局',
        	'社保局',
        	'林业局',
        	'工商局',
        	'税务局',
    			'公安局',
        	'人力局',
        	'林业局',
        	'工商局',
        	'市信用办'
        ],
        data:[],
        month:[]
      }
    },
    watch:{
      data(newVal){
        this.chartInit(newVal,this.month)
      }
    },
    mounted() {
      this.cityUnitEcharts = this.$echarts.init(this.$refs.cityUnitEcharts);
    	this.unitsList(this.units[0])
    },
    methods: {
    	unitsList(params){
    		HomeHttp.JbAnalysisList({
    			department:params
    		}).then((result)=>{
          this.data = [];
          this.month = [];
          result.data.forEach((item,index) =>{
          	this.data.push(item.COUNT)
          	this.month.push(item.MONTH)
          });
          if(result.data.length){
            this.chartInit(this.data,this.month)
          }
//          else{
//              document.getElementById('cityUnitEcharts').innerHTML = '<div style="line-height:447px;text-align:center;font-size:20px;">暂无数据</div>'
//          }
        }).catch(() => {

        });

    	},
		  doThis(index){
		    this.unitsList(this.units[index])
		  	this.isActiveIndex = index
		  },
			chartInit:function(data,month){
        this.cityUnitEcharts.setOption({
           tooltip: {
		            show: true,
		            trigger: 'axis',
		            formatter:'{c}条',
		    				backgroundColor:'rgba(0,0,0,0.65)',
		            borderColor:'rgba(0,0,0,0.65)',
		            borderWidth:1,
		            position:'top'
		        },
		        grid: {
		        	bottom:30,
		        	top:8
		        },
		        backgroundColor:'#fff',
		        color:['#1183ff',"#f8c100",'#ff0000'],
		        xAxis: {
		            type: 'category',
		            boundaryGap: false,//折线距离y轴有无距离
		            axisLabel: {
		                interval:0,
		                textStyle: {
		                    fontSize: 14,
		                    color: "#000",
		                },
		                margin: 10
		            },
		            axisTick: {
		                show:false
		            },
		            axisLine: {
		                lineStyle: {
		                    color: "#ccc",
		                    type: "solid"
		                }
		            },
		            splitLine:{
		                show:true,
		                lineStyle:{
		                    type:'dashed'
		                }
		            },
		            data :month //['2017-1','2017-2','2017-3','2017-4','2017-5','2017-6','2017-7','2017-8','2017-9','2017-10','2017-11','2017-12']
		        },
		        yAxis: [
		            {
		                type: 'value',
		                //max: '3000',
		                axisLabel: {
		                    margin: 10,
		                    textStyle: {
		                        fontSize: 14,
		                        color: "#000"
		                    },
		                    formatter:'{value}',
		                },
		                axisTick: {
		                    show: false
		                },
		                axisLine: {
		                     lineStyle: {
		                        color: '#ccc', //分割线颜色
		                        type: "dashed",
		                       // opacity:0.5
		                    }
		                },
		                splitLine: {
		                    //show:false,
		                    lineStyle: {
		                        color: '#ccc', //分割线颜色
		                        type: "dashed",
		                        //opacity:0.5
		                    }
		                },
		                splitArea: {
		                    show: true,
		                    areaStyle: {
		                        color: ['#fafcff', '#fff']
		                    }
		                }
		            },
		        ],
		        series : [
		             {
		                name:'数据',
		                type:'line',
		                //data:this._data,
		                data:data,
		                smooth: true,
		                //showSymbol: false,
		                symbolSize: 1,
		                areaStyle:{
		                    normal:{
		                        color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{
		                            offset: 0,
		                            color: '#a4ccfd'
		                        }, {
		                            offset: 0.5,
		                            color: 'rgba(17,131,255,0)'
		                        }])

		                    }
		                }
		            }
		        ]
        });
			}
    }
  }


</script>

<style lang="scss" scoped>

  .con1{
    width: 100%;
    min-height:calc(100vh - 120px);
    border-radius: 5px;
    background-color: #fff;
    .cityUnit{
    	box-sizing: border-box;
    	padding-top:35px;
    	padding-left:20px;position:relative;
    	.cityUnitName{
    		width:150px;
    		text-align:center;
    		border-radius: 3px;
    		padding-bottom:1px;
    		float:left;
    		p{
    			font-size:16px;
    			color:#333;
    			width:100%;
    			height:46px;
    			background:#efefef;
    			line-height:46px;
    			border-radius: 3px 3px 0px 0px;
    		}
    		ul{
    			width:100%;
    			border:1px solid #eff6ff;
    			border-top:none;
    			box-sizing: border-box;
    			li{
    				height:40px;
    				width:100%;
    				margin:0 auto;
    				line-height:40px;
    				cursor: pointer;
    				background:#fff;
    				&:hover{
    					background:#e9f2fd;
    				}
    			}
    		}
    	}
    	.cityUnitEcharts{
    		margin-left:170px;
    		height:447px;
    	}
    }
  }
  .active{background:#e9f2fd!important}
  .cityUnitNodata{line-height:447px;height:447px;text-align:center;font-size:20px;background:#fff;position:absolute;top:35px;right:0;z-index:1;width:calc(100% - 170px);pointer-events:none;}
</style>



