<template>
  <div class="header">
    <div class="hea_tit">
      <div class="ball">
        <span></span>
      </div>
      <span>考核任务查询</span>
      <!--<span class="span"><i class="iconfont icon-yanjing1"></i><span>隐藏</span></span>-->
    </div>
    <div class="hea_con">
      <ul>
        <li>
          <span>任务名称</span>
          <el-input v-model="objectData.input" placeholder="请输入内容"></el-input>
        </li>
        <li>
          <span>任务类型</span>
          <el-select v-model="objectData.value" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </li>
        <li>
          <span>任务状态</span>
          <el-select v-model="objectData.value1" placeholder="请选择">
            <el-option
              v-for="item in options2"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </li>
        <li class="el-input__Date">
          <span>时间</span>
          <el-date-picker
            v-model="objectData.Date1"
            type="date"
            :picker-options="pickerBeginDateBefore"
            placeholder="选择日期">
          </el-date-picker>   ---
          <el-date-picker
            v-model="objectData.Date2"
            type="date"
            :picker-options="pickerBeginDateAfter"
            placeholder="选择日期">
          </el-date-picker>
        </li>
        <li>
          <el-button type="primary" icon="el-icon-search" @click="queryObj"><span>查询</span></el-button>
          <button class="btn_cz" @click="reset"><i class="iconfont icon-xuanzhuanLeft"></i><span>重置</span></button>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="es6">
  export default {
    name: 'home',
    data(){
      return {
        currentPage:1,
        pickerBeginDateBefore:{
          disabledDate: (time) => {
            let beginDateVal = this.objectData.Date2;
            if (beginDateVal) {
              return time.getTime() > beginDateVal;
            }
          }
        },
        pickerBeginDateAfter:{
          disabledDate: (time) => {
            let beginDateVal = this.objectData.Date1;
            if (beginDateVal) {
              return time.getTime() < beginDateVal;
            }
          }
        },
        options: [{
          value:1,
          label: '周考核任务'
        }, {
          value:2,
          label: '月考核任务'
        }, {
          value:3,
          label: '季考核任务'
        }, {
          value:4,
          label: '年考核任务'
        }, {
          value:5,
          label: '动态考核任务'
        }],
        options2: [{
          value:1,
          label: '已完结'
        },{
          value:2,
          label: '已发布'
        },{
          value:3,
          label: '未启用'
        }],
        objectData:{
          value:'',
          value1: '',
          Date1:'',
          Date2:'',
          input:''
        },
      }
    },
    activated:function () {
      global.callBackFn = this.reset;
      this.reset();
    },
    methods:{
      queryObj(){
        this.$emit("query",this.objectData);
      },
      reset(){
        this.objectData ={
          value:'',
          value1: '',
          Date1:'',
          Date2:'',
          input:''
        };
        this.$emit("query",this.objectData);
      }
    }
  }
</script>

<style lang="scss" scoped>
  .header{
    width: 100%;
    height: 145px;
    background-color: #fff;
    border-radius:5px;
    .hea_con{
      ul{
        li:first-child{
          width: 24%;
          .el-input{
            width:70%;
          }
        }
        li:nth-child(2){
          width: 16%;
        }
        li:nth-child(3){
          width: 16%;
        }
        li:nth-child(4){
          width: 25%;
        }
        li:last-child{
          width: 19%;
        }
      }
    }
  }

  .el-select{
    /*width: 165px;*/
    width:60%;
    height: 33px;
    line-height: 33px;
    color: #ababab;
    border-radius: 5px;
  }
  .el-select>span{
    margin-left: 15px;
    i{
      /*margin-left:65px;*/
      position: absolute;
      right: 10px;
      top: 10px;
    }
  }
  .el-button{
    padding: 9px 21px;
  }
  .el-date-editor.el-input, .el-date-editor.el-input__inner{
    width:37%;
  }
</style>



