
<template>
  <div class="home-wrap">
    <headerIn4 @query="query"></headerIn4>
    <contentIn4 :weObject='weObject'></contentIn4>
  </div>
</template>

<script type="es6">
  import headerIn4 from "./headerIn4";
  import contentIn4 from "./contentIn4";
  import {puHttp} from '../../api/puHttp'
  export default {
    name: 'home',
    data(){
      return {
        weObject:{
          taskName:'',
          type:'',
          status:'',
          startTime:'',
          endTime:'',
        },
      }
    },
    methods:{
      query(data) {
        this.weObject.taskName=data.input
        this.weObject.type=data.value
        this.weObject.status=data.value1
        if (data.Date1!=''){
          this.weObject.startTime=this.dateObj(data.Date1)
        }else {
          this.weObject.startTime=""
        }
        if (data.Date2!=''){
          this.weObject.endTime=this.dateObj(data.Date2)
        }else {
          this.weObject.endTime=""
        }
      }
    },
    components:{
      headerIn4,
      contentIn4
    },
  }
</script>

<style lang="scss" scoped>
</style>



