<template>
  <div class="con">
    <div class="hea_tit">
      <div class="ball">
        <span></span>
      </div>
      <span>考核任务列表</span>
      <router-link :to="{path:'/home/AssessmentTask/home4Added', query:{}}"><button class="btn2"><i class="el-icon-plus"></i><span>新增</span></button></router-link>
    </div>
    <div class="table-warp">
      <table>
        <tr class="thead">
          <td>序号</td>
          <td>考核名称</td>
          <td>发起时间</td>
          <td>截止时间</td>
          <td>任务类型</td>
          <td>任务状态</td>
          <td>操作</td>
        </tr>
        <tr v-if="list.length > 0" v-for="(item,idx) in list">
          <td>{{idx+1}}</td>
          <td>{{item.ASSESS_NAME | isValue}}</td>
          <td>{{item.START_TIME | isValue}}</td>
          <td>{{item.END_TIME | isValue}}</td>
          <td v-if="item.TYPE==1">{{"周考核任务" | isValue}}</td>
          <td v-if="item.TYPE==2">{{"月考核任务" | isValue}}</td>
          <td v-if="item.TYPE==3">{{"季考核任务" | isValue}}</td>
          <td v-if="item.TYPE==4">{{"年考核任务" | isValue}}</td>
          <td v-if="item.TYPE==5">{{"动态考核任务" | isValue}}</td>
          <td v-if="item.STATUS==1">已完结</td>
          <td v-if="item.STATUS==2">已发布</td>
          <td v-if="item.STATUS==3">未启用</td>
          <td>
            <router-link :to="{path:'/home/AssessmentTask/home4Details', query:{id:item.ID}}" v-if="item.STATUS==1||item.STATUS==2"><button><i class="iconfont icon-yanjing"></i><span>查看</span></button></router-link>
            <router-link :to="{path:'/home/AssessmentTask/home4Modify', query:{id:item.ID}}"><button v-if="item.STATUS==3"><i class="iconfont icon-cloud-modified"></i><span>修改</span></button></router-link>
            <button class="sc"  v-if="item.STATUS==3" v-on:click="confirmDeletion(idx,item)"><i class="iconfont icon-shanchu"></i><span>刪除</span></button>
          </td>
        </tr>
        <tr v-if="list.length === 0" class="noDataTr">
          <span>暂无数据</span>
        </tr>
      </table>
      <div class="pagination fr">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="10"
          layout="prev, pager, next, jumper"
          :total=this.count>
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script type="es6">
  import {puHttp} from '../../api/puHttp'
  export default {
    name: 'home',
    props: {
      weObject: {
        type: Object,
        default:{}
      },
    },
    data(){
      return {
        currentPage:1,
        list:[],
        count:0,
        nameId:'',
      }
    },
//    mounted() {
//      this.queryList();
//    },
    activated(){
      this.currentPage = 1;
      this.queryList();
    },
    methods: {
      confirmDeletion(index,item) {
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          puHttp.tackSc({
            id:item.ID
          }).then((result)=>{
            this.list.splice(index,1)
            this.queryList();
          }) .catch((error) => {
            this.openObject("服务器连接超时,请稍后再试");
          })
          this.$message({
            type: 'success',
            message: '删除成功!',
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.queryList();
      },
      queryList(){
        const parData={
          page:this.currentPage,
          rows:10
        };
        if (this.weObject.taskName!=''){
          parData.taskName=this.weObject.taskName
        };
        if (this.weObject.type!=''){
          parData.type=this.weObject.type
        };
        if (this.weObject.status!=''){
          parData.status=this.weObject.status
        };
        if (this.weObject.startTime!=''){
          parData.startTime=this.weObject.startTime
        };
        if (this.weObject.endTime!=''){
          parData.endTime=this.weObject.endTime
        };
        puHttp.queryList(parData).then((result)=>{
          this.list = result.data;
          this.count= result.count
        }) .catch((error) => {
          this.openObject("服务器连接超时,请稍后再试")
          })
      },
    },
    filters: {
      isValue(val) {
        return (val = val == "" ? "-" : val);
      }
    },
    watch:{
      weObject: {
        handler: function (val, oldVal) {
          this.queryList();
        },
        deep: true
      },
    }
  }
</script>

<style lang="scss" scoped>
  .con{
    margin-top: 20px;
  }
</style>



