<template>
  <div class="con">
    <div class="hea_tit">
      <div class="ball">
        <span></span>
      </div>
      <span>考核任务详情</span>
    </div>
    <div class="box">
      <ul>
        <li>
          <span>任务名称 : </span>
          <span>{{list.ASSESS_NAME | isValue}}</span>
        </li>
        <li>
          <span>任务类型 : </span>
          <span v-if="list.TYPE==1">{{"周考核任务" | isValue}}</span>
          <span v-if="list.TYPE==2">{{"月考核任务" | isValue}}</span>
          <span v-if="list.TYPE==3">{{"季考核任务" | isValue}}</span>
          <span v-if="list.TYPE==4">{{"年考核任务" | isValue}}</span>
          <span v-if="list.TYPE==5">{{"动态考核任务" | isValue}}</span>
        </li>
        <li>
          <span>任务发布日期 : </span>
          <span>{{list.START_TIME | isValue}}</span>
        </li>
        <li>
          <span>任务截至日期 : </span>
          <span>{{list.END_TIME | isValue}}</span>
        </li>
        <li>
          <span>考核部门 : </span>
          <span :title="list.ASSESS_DEPARTMENT">{{list.ASSESS_DEPARTMENT | isValue}}</span>
        </li>
        <li>
          <span>考核数据 : </span>
          <span>{{list.ASSESS_DATA | isValue}}</span>
        </li>
        <li>
          <span>考核要求 : </span>
          <span>{{list.ASSESS_REQUIRE | isValue}}</span>
        </li>
        <li>
          <router-link to="/home/AssessmentTask"><button class="btn3">返回</button></router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="es6">
  import {puHttp} from '../../api/puHttp'
  export default {
    name: 'home',
    data(){
      return {
        currentPage:1,
        list:[],
        nameId:''
      }
    },
    activated(){
      this.nameId = this.$route.query.id
      this.deList()
    },
    methods:{
      deList(){
        puHttp.tackDe({
          id:this.nameId
        }).then((result)=>{
          this.list=result.data
        }).catch((error) => {
          this.openObject("服务器连接超时,请稍后再试")
        })
      }
    },
   filters: {
     isValue(val) {
       return (val = val == "" ? "无" : val);
     }
   },
  }
</script>

<style lang="scss" scoped>
  .con{
    width: 100%;
    background-color: #fff;
    min-height: calc(100vh - 120px);
    margin-top: 0 !important;
  }
  .box{
    width: 100%;
    ul{
      width: 100%;
      margin-top: 30px;
      li{
        width: 100%;
        height: 60px;
        line-height: 60px;
        position: relative;
        span{
          height: 100%;
          display: inline-block;
          float: left;
          font-size: 14px;
          box-sizing: border-box;
        };
        span:first-child{
          width: 45%;
          text-align: right;
          color: #acacac;
          padding-right: 10px;
        }
        span:last-child{
          width:50%;
          text-align: left;
          color: #5b5b5b;
          padding-left:10px;
          overflow:hidden;
          white-space:nowrap;
          text-overflow:ellipsis;
        }
      }
      li:last-child{
        margin-top: 30px;
      }
    }
  }
  .btn3{
    width: 90px;
    height: 35px;
    line-height: 35px;
    background: #4d9cf7;
    color: #fff;
    text-align: center;
    font-size: 14px;
    border: 1px solid #3a81f7;
    border-radius:5px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
  }
  .btn3:hover{
    text-decoration: underline;
    opacity: 0.9;
  }
</style>



