<template>
    <div class="header-wrap">
      <div class="zuo">
        <router-link class="item" tag="li" to="/home/home1">
          <img src="../assets/imgs/logo.png">
          <span>泸州市绩效考核系统</span>
        </router-link>
      </div>
      <div class="sign">
        <span>admin</span>
        <div class="portrait">
          <img src="../assets/imgs/touxiang.png"/>
        </div>
      </div>
    </div>
</template>

<script type="text/javascript">
  export default {
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";
  .header-wrap{
    width: 100%;
    height: 80px;
    line-height:80px;
    padding-left: 20px;
    box-sizing: border-box;
    background:url("../assets/imgs/header_bg.png") no-repeat;
    background-size: 100% 100%;
    position: relative;
    .zuo{
      cursor: pointer;
    }
    span{
      color: #fff;
      font-size: 26px;
      margin-left: 20px;
      float: left;
    }
    img{
      position: relative;
      top: 15px;
      float: left;

    }
    .sign{
      width: 110px;
      height: 100%;
      float: right;
      margin-right: 40px;
      position: relative;
      span{
        font-size: 16px;
        position: absolute;
        left: -15px;
      }
      .portrait{
        width: 46px;
        height: 46px;
        position: absolute;
        right:0;
        top:0;
        img{
          width: 100%;
          height: 100%;
        }
      }
    }
  }

</style>




