<template>
  <div class="con">
    <div class="hea_tit">
      <div class="ball">
        <span></span>
      </div>
      <span>考核结果列表</span>
    </div>
    <div class="table-warp">
      <table>
        <tr class="thead">
          <td>序号</td>
          <td>考核名称</td>
          <td>任务类型</td>
          <td>操作</td>
        </tr>
        <tr v-if="list.length > 0" v-for="(item,idx) in list">
          <td>{{idx+1}}</td>
          <td>{{item.ASSESS_NAME}}</td>
          <td v-if="item.TYPE==1">{{"周考核任务" | isValue}}</td>
          <td v-if="item.TYPE==2">{{"月考核任务" | isValue}}</td>
          <td v-if="item.TYPE==3">{{"季考核任务" | isValue}}</td>
          <td v-if="item.TYPE==4">{{"年考核任务" | isValue}}</td>
          <td v-if="item.TYPE==5">{{"动态考核任务" | isValue}}</td>
          <td>
            <router-link :to="{path:'/home/ResultQuery/home5Details', query:{id:item.ID,listName:item.ASSESS_NAME}}"><button><i class="iconfont icon-yanjing"></i><span>查看</span></button></router-link>
            <button @click="download(item)" class="xz"><i class="iconfont el-icon-download"></i><span>下载</span></button>
          </td>
        </tr>
        <tr v-if="list.length === 0" class="noDataTr">
          <span>暂无数据</span>
        </tr>
      </table>
      <div class="pagination fr">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="10"
          layout="prev, pager, next, jumper"
          :total=this.count>
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script type="es6">
  import {puHttp} from '../../api/puHttp'
  export default {
    name: 'home',
    props: {
      weObject: {
        type: Object,
        default:{}
      },
    },
    data(){
      return {
        currentPage:1,
        list:[],
        count:0,
        nameId:'',
      }
    },
//    mounted() {
//      this.currentPage = 1;
//      this.queryList();
//    },
    activated(){
      this.currentPage = 1;
      this.queryList();
    },
    methods: {
      handleCurrentChange(val) {
        this.currentPage = val;
        this.queryList();
      },
      queryList(){
        const parData={
          page:this.currentPage,
          rows:10
        }
        if (this.weObject.quName!=''){
          parData.taskName=this.weObject.quName
        };
        if (this.weObject.quType!=''){
          parData.type=this.weObject.quType
        };
        if (this.weObject.startDate!=''){
          parData.startTime=this.weObject.startDate
        };
        if (this.weObject.endDate!=''){
          parData.endTime=this.weObject.endDate
        };
        puHttp.queryList(parData).then((result)=>{
          this.list = result.data;
          this.count= result.count
        }).catch((error) => {
          this.openObject("服务器连接超时,请稍后再试")
        });
      },
      //下载
      download(downloadId){
        puHttp.resultDownload({
          taskId:downloadId.ID
        }).then((result)=>{
          let base_url = window.BASE_URL;
          location.href=base_url+'/performance/downloadassess?taskId='+downloadId.ID
        }).catch((error) => {
          this.openObject("服务器连接超时,请稍后再试")
        })
      }
    },
    filters: {
      isValue(val) {
        return (val = val == "" ? "-" : val);
      }
    },
    watch:{
      weObject: {
        handler: function (val, oldVal) {
          this.queryList();
        },
        deep: true
      }
    }
  }
</script>

<style lang="scss" scoped>
  .con{
    margin-top: 20px;
    width: 100%;
    /*height: 795px;*/
    background-color: #fff;
  }
</style>



