<template>
  <div class="home-wrap">

    <div class="header">
      <div class="hea_tit">
        <div class="ball">
          <span></span>
        </div>
        <span>数据异常预警</span>
        <!--<span class="span"><i class="iconfont icon-yanjing1"></i><span>隐藏</span></span>-->
      </div>
      <div class="hea_con">
        <ul>
          <li>
            <span>单位</span>
            <el-input v-model="input" placeholder="请输入单位"></el-input>
          </li>
          <li class="el-input__Date">
            <span>开始时间</span>
            <el-date-picker
              v-model="Date1"
              type="date"
              :picker-options="pickerBeginDateBefore"
              placeholder="选择日期">
            </el-date-picker>   ---
            <el-date-picker
              v-model="Date2"
              type="date"
              :picker-options="pickerBeginDateAfter"
              placeholder="选择日期">
            </el-date-picker>
          </li>
          <li>
            <el-button type="primary" icon="el-icon-search" @click="homeSearch"><span>查询</span></el-button>
            <button class="btn_cz" @click="reset"><i class="iconfont icon-xuanzhuanLeft"></i><span>重置</span></button>
          </li>
        </ul>
      </div>
    </div>

    <div class="con">
      <div class="hea_tit">
        <div class="ball">
          <span></span>
        </div>
        <span>考核结果列表</span>

      </div>
      <div class="table-warp">
        <table>
          <tr class="thead">
            <td>序号</td>
            <td>单位</td>
            <td>异常原因</td>
            <td>时间</td>
            <td>操作</td>
          </tr>
          <tr v-if="!dataErrorList.length" class="noDataTr">
            <td colspan="5" class="font20">暂无数据</td>
          </tr>
          <tr v-else v-for="(item,idx) in dataErrorList">
            <td>{{idx+1}}</td>
            <td>{{item.COMPANY}}</td>
            <td>{{item.REASON}}</td>
            <td>{{item.OCCUR_DATE}}</td>
            <td>
              <router-link :to="{path:'/home/home1/HomeDet', query:{id:item.ID}}"><button><i class="iconfont icon-yanjing"></i><span>查看</span></button></router-link>
            </td>
          </tr>

        </table>
        <div class="pagination fr">
          <el-pagination
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size=this.pageSize
            layout="prev, pager, next, jumper"
            :total=this.allData>
          </el-pagination>
        </div>
      </div>
    </div>

  </div>
</template>

<script type="es6">
  import {HomeHttp} from '../../api/homeHttp';
  export default {
    name: 'homeList',
    data(){
      return {
        Date1:'',
        Date2:'',
        input:'',
        currentPage:1,
        dataErrorList:[],
        pageSize:10,
        allData:0,
        pickerBeginDateBefore:{
          disabledDate: (time) => {
            let beginDateVal = this.Date2;
            if (beginDateVal) {
              return time.getTime() > beginDateVal;
            }
          }
        },
        pickerBeginDateAfter:{
          disabledDate: (time) => {
            let beginDateVal = this.Date1;
            if (beginDateVal) {
              return time.getTime() < beginDateVal;
            }
          }
        }
      }
    },
    created:function(){
      this.initData();
    },
    mounted(){

    },
    methods: {
      initData: function () {
        this.queryDataerror();
      },
      homeSearch:function () {
        this.queryDataerror();
      },
      reset(){
        this.Date1='';
        this.Date2='';
        this.input='';
        this.queryDataerror()
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.queryDataerror();
      },
      queryDataerror(){
        const parData={
          page:this.currentPage,
          rows:this.pageSize
        }
        if (this.input!=''){
          parData.company=this.input
        };
        if (this.Date1!=''){
          parData.startTime=this.dateObj(this.Date1)
        };
        if (this.Date2!=''){
          parData.endTime=this.dateObj(this.Date2)
        };
        HomeHttp.queryHomeDataerror(parData).then((result)=>{
          this.allData = result.count;
          this.dataErrorList = result.data;
        }).catch(() => {});
      }
    }
  }
</script>

<style lang="scss" scoped>
  .header{
    width: 100%;
    height: 145px;
    background-color: #fff;
    border-radius:3px;
    .hea_con{
      ul{
        li:first-child{
           width: 25%;
          .el-input{
            width:70%;
          }
        }
        li:nth-child(2){
          width: 40%;
        }
      }
    }
  }

  .el-button{
    padding: 9px 21px;
  }
  .el-date-editor.el-input, .el-date-editor.el-input__inner{
    width:37%;
  }
  .con{
    margin-top: 20px;
    width: 100%;
    background-color: #fff;
  }
  .font20{font-size:20px;}
</style>



