import Vue from 'vue'
import Router from 'vue-router'
import Box from 'pages/main'
import HomeLeftMenu from 'components/home-left-menu'
import Home from 'pages/home/home'
import HomeList from 'pages/home/homeList'
import HomeDet from 'pages/home/homeDet'
import AsAnalysis from 'pages/AsAnalysis/home2'
import JbAnalysis from 'pages/JbAnalysis/home3'
import AssessmentTask from 'pages/AssessmentTask/AssessmentTask'
import ResultQuery from 'pages/ResultQuery/home5'
import AssessmentReport from 'pages/AssessmentReport/home6'
import conBody from 'pages/content'
import home4Added from 'pages/AssessmentTask/home4Added'
import home4Modify from 'pages/AssessmentTask/home4Modify'
import home4Details from 'pages/AssessmentTask/home4Details'
import home5Details from 'pages/ResultQuery/home5Details'
import monthTab from 'pages/AssessmentReport/monthTab'
import seasonTab from 'pages/AssessmentReport/seasonTab'
import yearTab from 'pages/AssessmentReport/yearTab'
import details from 'pages/AssessmentReport/details'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: 'home'
    },
    {
      path: '/home',
      component: Box,
      children: [
        {
          path: '',
          redirect: 'home1'
        },
        {
          path: 'home1',
          components: {
            leftMenu: HomeLeftMenu,
            body: conBody
          },
          children:[
            {
              path: '',
              components: {
                RoutList: Home
              }
            },
            {
              path: 'HomeList',
              components: {
                RoutList: HomeList
              }
            },
            {
              path: 'HomeDet',
              components: {
                RoutList: HomeDet
              }
            }
          ]
        },
        {
          path: 'home2',
          components: {
            leftMenu: HomeLeftMenu,
            body: AsAnalysis
          }
        },
        {
          path: 'JbAnalysis',
          components: {
            leftMenu: HomeLeftMenu,
            body: JbAnalysis
          }
        },
        {
          path: 'AssessmentTask',
          components: {
            leftMenu: HomeLeftMenu,
            body: conBody
          },
          children:[
            {
              path: '',
              redirect: 'list',
            },
            {
              path: 'list',
              components: {
                RoutList: AssessmentTask
              }
            },
            {
              path: 'home4Details',
              components: {
                RoutList: home4Details
              }
            },
            {
              path: 'home4Modify',
              components: {
                RoutList: home4Modify
              }
            },
            {
              path: 'home4Added',
              canReuse:false,
              components: {
                RoutList: home4Added
              }
            },
          ]
        },
        {
          path: 'ResultQuery',
          components: {
            leftMenu: HomeLeftMenu,
            body: conBody,
          },
          children:[
            {
              path: '',
              redirect: 'list',
            },
            {
              path: 'list',
              components: {
                RoutList: ResultQuery
              }
            },
            {
              path: 'home5Details',
              components: {
                RoutList: home5Details
              }
            },
          ]
        },
        {
          path: 'AssessmentReport',
          components: {
            leftMenu: HomeLeftMenu,
            body: conBody
          },
          children:[
            {
              path: '',
              components: {
                RoutList: AssessmentReport
              },
              children:[
                {
                  path: '',
                  redirect: 'monthTab',
                },
                {
                  path: 'monthTab',
                  components: {
                    bodyList: monthTab
                  },
                  mate:{
                    keepAlive:false
                  }
                },
                {
                  path: 'seasonTab',
                  components: {
                    bodyList: seasonTab
                  },
                  mate:{
                    keepAlive:false
                  }
                },
                {
                  path: 'yearTab',
                  components: {
                    bodyList: yearTab
                  },
                  mate:{
                    keepAlive:false
                  }
                },
              ]
            },
            {
              path: 'details',
              components: {
                RoutList: details
              },
              mate:{
                keepAlive:false
              }
            },
          ]
        },
        {
          path: 'details',
          components: {
            leftMenu: HomeLeftMenu,
            body: details,
          },
          mate:{
            keepAlive:false
          }
        },
      ]
    },
  ],
  linkActiveClass: 'active'
});



