<template>
  <div>
  <div class="home-wrap">
    <el-row :gutter="20" class="h655">
      <el-col :span="15">
        <div class="ldmodle modle1">
          <div class="mokuai mokuai1">
            <p class="mokuai-t" :title="`监控单位：${jiankong.COUNT}家`">监控单位：<span>{{jiankong.COUNT}}</span>家</p>
            <div class="mokuai-b">
              <p>{{jiankong.PLACE}}</p>
              <p :title="`去年同期：${jiankong.LINK_RELATIVE_RATIO}%上升${jiankong.RISE_RATIO}%`">去年同期：<span>{{jiankong.LINK_RELATIVE_RATIO}}%</span>；上升<span>{{jiankong.RISE_RATIO}}%</span><i class="iconfont icon-jiantou-copy"></i></p>
            </div>
          </div>
          <div class="mokuai mokuai3">
            <p class="mokuai-t" :title="`全国排名：${top.RANKING}名`">全国排名：<span>{{top.RANKING}}</span>名</p>
            <div class="mokuai-b">
              <p>{{top.PLACE}}</p>
              <p :title="`去年同期：${top.SAME_PERIOD_LAST_YEAR}名上升${top.RISE_RATIO}名`">去年同期：<span>{{top.SAME_PERIOD_LAST_YEAR}}名</span>；上升<span>{{top.RISE_RATIO}}名</span><i class="iconfont icon-jiantou-copy"></i></p>
            </div>
          </div>
          <div class="mokuai mokuai2">
            <p class="mokuai-t" :title="`数据总量：${sjzl.COUNT}万条`">数据总量：<span>{{sjzl.COUNT}}</span>万条</p>
            <div class="mokuai-b">
              <p>{{sjzl.PLACE}}</p>
              <p :title="`去年同期：${sjzl.LINK_RELATIVE_RATIO}%上升${sjzl.RISE_RATIO}%`">去年同期：<span>{{sjzl.LINK_RELATIVE_RATIO}}%</span>；上升<span>{{sjzl.RISE_RATIO}}%</span><i class="iconfont icon-jiantou-copy"></i></p>
              <!--<p>去年同期：<span>{{sjzl.PLACE}}%</span>；下降<span>{{sjzl.PLACE}}%</span><i class="iconfont icon-keybown"></i></p>-->
            </div>
          </div>

        </div>
        <div class="ldmodle modle2">
          <div class="hea_tit">
            <div class="ball"><span ></span></div><span>数据上传整体增长趋势</span>
          </div>
          <div id="ldchart1" class="ldchart1"></div>
        </div>
      </el-col>
      <el-col :span="9">
        <div class="ldmodle modle3">
          <div class="hea_tit">
            <div class="ball"><span ></span></div><span>上传数据排行</span>
          </div>
          <div class="ldtb">
            <table>
              <tr>
                <th>排行</th>
                <th>单位</th>
                <th>数据总量</th>
                <th>更新频率</th>
              </tr>
              <tr v-if="!dataErrorList.length" class="noDataTr">
                <td colspan="5" class="font20">暂无数据</td>
              </tr>
              <tr v-else v-for="(item,index) in uploadList">
                <td><span class="list-num">{{index+1}}</span></td>
                <td>{{item.DEPARTMENT}}</td>
                <td>{{item.COUNT}}条</td>
                <td>／{{item.FREQUENCY}}</td>
              </tr>
            </table>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="20" class="mt20">
      <el-col :span="12">
        <div class="ldmodle modle4">
          <div class="hea_tit">
            <div class="ball"><span ></span></div><span>数据综合分析</span>
          </div>
          <div id="ldchart2" class="ldchart2"></div>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="ldmodle modle5">
          <div class="hea_tit">
            <div class="ball"><span ></span></div><span>数据异常预警</span>
            <router-link :to="{path:'/home/home1/HomeList', query:{}}"> <span class="sjycmore">>></span> </router-link>
          </div>
          <div v-if="!dataErrorList.length" class="ldlist-nodata">暂无数据</div>
          <ul v-else class="ldlist">
            <li v-for="item in dataErrorList">
              <router-link :to="{path:'/home/home1/HomeDet', query:{id:item.ID}}">{{item.REASON}}<i class="iconfont icon-gantanhao"></i></router-link>
              <span>{{item.OCCUR_DATE}}</span>
            </li>
          </ul>
        </div>
      </el-col>
    </el-row>
  </div>
  </div>
</template>

<script type="es6">
  import {HomeHttp} from '../../api/homeHttp';
  export default {
    name: 'home',
    data(){
      return {
        chart1data:{
          month:[],
          data1:[],
          data2:[],
          data3:[]
        },
        chart2data:{
          data1:[],
          data2:[],
          data3:[]
//          data1:[{name : '工商局',value: [2.3,7.5]},{name : '公安局',value: [6.8,7.2]},{name : '卫生局',value: [1.5,1.1]}],
//          data2:[{name : '人社局',value: [5.4, 5.4]},{name : '审计局',value: [2.5,2.7]}],
//          data3:[{name : '环保局', value: [1.1, 5.6]},{name : '食药监', value:  [5.9,3.4]}]
        },
        jiankong:[],
        sjzl:[],
        top:[],
        uploadList:[],
        dataErrorList:[]
      }
    },
    created:function(){
      this.initData();
    },
    mounted(){
      //this.chart1();
      //this.chart2();

    },
    methods: {
      initData: function () {
        this.queryJiankong();
        this.querySjzl();
        this.queryTop();
        this.queryChart1();
        this.queryUpload();
        this.queryChart2();
        this.queryDataerror();
      },
      chart1:function(){
        let ldchart1 = this.$echarts.init(document.getElementById('ldchart1'));
        ldchart1.setOption({
          tooltip: {
            show: true,
            trigger: 'axis',
            //formatter:'{a}：{c}',
            backgroundColor:'rgba(0,0,0,0.65)',
            borderColor:'rgba(0,0,0,0.65)',
            borderWidth:1
          },
          legend:{
            y:10,
            textStyle: {
              color: '#000'
            },
            itemHeight:2,
            itemWidth:25,
            icon:'rect',
            data:['数量','质量','频率']
          },
          color:['#1183ff',"#f8c100",'#ff0000'],
          xAxis: {
            type: 'category',
            boundaryGap: false,//折线距离y轴有无距离
            axisLabel: {
              interval:0,
              textStyle: {
                fontSize: 14,
                color: "#000",
              },
              margin: 10
            },
            axisTick: {
              show:false
            },
            axisLine: {
              lineStyle: {
                color: "#ccc",
                type: "solid"
              }
            },
            splitLine:{
              show:true,
              lineStyle:{
                type:'dashed'
              }
            },
            data : this.chart1data.month
          },
          yAxis: [
            {
              type: 'value',
              axisLabel: {
                margin: 10,
                textStyle: {
                  fontSize: 14,
                  color: "#000"
                },
                formatter:'{value}',
              },
              axisTick: {
                show: false
              },
              axisLine: {
                lineStyle: {
                  color: '#ccc', //分割线颜色
                  type: "dashed",
                  // opacity:0.5
                }
              },
              splitLine: {
                //show:false,
                lineStyle: {
                  color: '#ccc', //分割线颜色
                  type: "dashed",
                  //opacity:0.5
                }
              },
              splitArea: {
                show: true,
                areaStyle: {
                  color: ['#fafcff', '#fff']
                }
              }
            }
          ],
          series : [
            {
              name:'数量',
              type:'line',
              data:this.chart1data.data1,
              smooth: true,
              symbolSize: 0,
              areaStyle:{
                normal:{
                  color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgba(17,131,255,.2)'
                  }, {
                    offset: 0.9,
                    color: 'rgba(17,131,255,0)'
                  }])

                }
              }
            },
            {
              name:'质量',
              type:'line',
              data:this.chart1data.data2,
              smooth: true,
              symbolSize: 0,
              areaStyle:{
                normal:{
                  color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgba(248,193,0,.2)'
                  }, {
                    offset: 0.9,
                    color: 'rgba(248,193,0,0)'
                  }])

                }
              }
            },
            {
              name:'频率',
              type:'line',
              data:this.chart1data.data3,
              smooth: true,
              symbolSize: 0,
              areaStyle:{
                normal:{
                  color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgba(255,0,0,.2)'
                  }, {
                    offset: 0.9,
                    color: 'rgba(255,0,0,0)'
                  }])

                }
              }
            }
          ]
      });
      },
      chart2:function () {
        let ldchart2 = this.$echarts.init(document.getElementById('ldchart2'))
        ldchart2.setOption({
          tooltip: {
            trigger: 'axis',
            showDelay: 0,
            formatter:'{b}\n{c}',
            axisPointer: {
              show: true,
              type: 'cross',
              crossStyle: {
                type: 'dashed ',
                width:1,
                color:"#3ff6f2",
                textStyle:{
                  color:"#fff"
                }
              }
            }
          },
          color:['#5798f7','#fc7054','#ffc000',"#a74fb1"],
          xAxis: [{
            name:'数据\n质量',
            type: 'value',
            //max:50,
            nameTextStyle:{
              fontSize:16
            },
            splitLine: {
              show: false
            },
            axisLabel:{
              textStyle:{
                fontSize:16,
                color:'#666'
              }
            },
            axisTick:{
              show:false
            },
            axisLine : {    // 轴线
              show: true,
              lineStyle: {
                color: '#666',
                type: 'solid',
                width: 1,

              }
            }
          }],
          yAxis: [{
            name:'数据数量',
            type: 'value',
            //max:200,
            nameTextStyle:{
              fontSize:16
            },
            splitLine: {
              show: false
            },
            axisLabel:{
              textStyle:{
                fontSize:16,
                color:'#666'
              }
            },
            axisTick:{
              show:false
            },
            axisLine : {    // 轴线
              show: true,
              lineStyle: {
                color: '#666',
                type: 'solid',
                width:1
              }
            }
          }],
          grid: {
            bottom:30,
            left:30,
            containLabel: true
          },
          series: [{
            //name: '第一产业',
            type: 'scatter',
            symbol: 'circle',
            symbolSize: 30,
            itemStyle: {
              normal: {
                label: {
                  show:true,
                  formatter: function(param) {
                    return param.name;
                  },
                  position: 'right',
                  textStyle: {
                    color: '#666',
                    fontSize:10
                  }
                }
              }
            },
            data: this.chart2data.data1,
//            markLine: {
//              itemStyle: {
//                normal: {
//                  lineStyle: {
//                    type: 'solid',
//                    color: '#c8d4e8'
//                  }
//                },
//                emphasis:{
//                  lineStyle: {
//                    width:1,
//                    type: 'solid',
//                    color: '#c8d4e8'
//                  }
//                }
//              },
//              symbol: ['none', 'none'],
//              data: [
//                [{
//                  value: 50,
//                  xAxis: 0,
//                  yAxis: 100
//                }, // 当xAxis或yAxis为数值轴时，不管传入是什么，都被理解为数值后做空间位置换算
//                  {
//                    xAxis: 50,
//                    yAxis: 100
//                  }
//                ],
//                [{
//                  value: 200,
//                  xAxis: 25,
//                  yAxis: 0
//                }, // 当xAxis或yAxis为数值轴时，不管传入是什么，都被理解为数值后做空间位置换算
//                  {
//                    xAxis: 25,
//                    yAxis: 200
//                  }
//                ]
//              ]
//            }
          },
            {
              //name: '第二产业',
              type: 'scatter',
              symbol: 'circle',
              symbolSize: 30,
              itemStyle: {
                normal: {
                  label: {
                    show:true,
                    formatter: function(param) {
                      return param.name;
                    },
                    position: 'right',
                    textStyle: {
                      color: '#666',
                      fontSize:10
                    }
                  }
                }
              },
              data: this.chart2data.data2
            },
            {
              //name: '第三产业',
              type: 'scatter',
              symbol: 'circle',
              symbolSize: 30,
              itemStyle: {
                normal: {
                  label: {
                    show:true,
                    formatter: function(param) {
                      return param.name;
                    },
                    position: 'right',
                    textStyle: {
                      color:'#666',
                      fontSize:10
                    }
                  }
                }
              },
              data: this.chart2data.data3
            }]
        });
      },
      queryJiankong(){
        HomeHttp.queryHomeJiankong({}).then((result)=>{
          this.jiankong = result.data;
        }).catch(() => {});
      },
      querySjzl(){
        HomeHttp.queryHomeSjzl({}).then((result)=>{
          this.sjzl = result.data;
        }).catch(() => {});
      },
      queryTop(){
        HomeHttp.queryHomeTop({}).then((result)=>{
          this.top = result.data;
        }).catch(() => {});
      },
      queryChart1(){
        HomeHttp.queryHomeChart1({}).then((result)=>{
          if(result.data.length == 0){
            document.getElementById("ldchart1").innerHTML='<div style="line-height:352px;text-align:center;font-size:20px;">暂无数据</div>';
          }else{
            for(let i=0;i<result.data.length;i++){
              this.chart1data.month.push(result.data[i].MONTH);
              this.chart1data.data1.push(result.data[i].COUNT);
              this.chart1data.data2.push(result.data[i].QUALITY);
              this.chart1data.data3.push(result.data[i].FREQUENCY);
            }
            this.chart1();
          }
        }).catch(() => {});
      },
      queryUpload(){
        HomeHttp.queryHomeUpload({}).then((result)=>{
            this.uploadList = result.data;
        }).catch(() => {});
      },
      queryChart2(){
        HomeHttp.queryHomeChart2({}).then((result)=>{
          if(result.data.length == 0){
            document.getElementById("ldchart2").innerHTML='<div style="line-height:620px;text-align:center;font-size:20px;">暂无数据</div>';
          }else{
            for(let i=0;i<result.data.length;i++){
              this.chart2data.data1.push({name : result.data[i].DEPARTMENT,value: [result.data[i].QUALITY,result.data[i].COUNT]})
            }
            this.chart2();
          }
        }).catch(() => {});
      },
      queryDataerror(){
        HomeHttp.queryHomeDataerror({
          page:1,
          rows:15
        }).then((result)=>{
          this.dataErrorList = result.data;
        }).catch(() => {});
      }
    }
  }
</script>

<style lang="scss" scoped>
  .home-wrap{
    padding-bottom: 20px;
  }
  .mt20{margin-top:20px;}
  .h655{height:636px;}
  .sjycmore{float:right;margin-right:20px;}
  .ldmodle{
    background: #fff;border-radius:4px;border:1px solid #f2f2f2;
    &.modle1{
      height:160px;padding:20px;overflow:hidden;
      .mokuai{
        width:31%;padding:0 3%;height:160px;box-sizing: border-box;margin:0 auto;border-radius:3px;
        &.mokuai1{float:left;background:linear-gradient(210deg,#5d9efe,#3d8afb);box-shadow:0 6px 18px rgba(129,180,254,.45);}
        &.mokuai2{background:linear-gradient(210deg,#ff8468,#fc7054);box-shadow:0 6px 18px rgba(254,135,128,.45);}
        &.mokuai3{float:right;background:linear-gradient(210deg,#fdac63,#fb9941);box-shadow:0 6px 18px rgba(252,173,103,.45);}
        .mokuai-t{
          height:65px;line-height:65px;overflow: hidden;text-overflow:ellipsis;white-space: nowrap;padding:0 3px;font-size:20px;color:#fff;border-bottom:1px solid rgba(255,255,255,.4);
          span{font-size:32px;}
        }
        .mokuai-b{
          padding:15px 0;
          p{
            height:26px;line-height:26px;color:#fff;overflow: hidden;text-overflow:ellipsis;white-space: nowrap;
            &:first-child{font-size:16px;}
            &:last-child{
               font-size:14px;
                span{font-size:18px;}
            }
          }
        }
      }
    }
    &.modle2{
      margin-top:20px;
      .ldchart1{
        height:352px;width:100%;
      }
    }
    &.modle3{
      height:635px;
      .ldtb{
        padding:13px 30px;
        table{
          width:100%;
          th,td{text-align:center;}
          th{background:#f6f6f6;font-size:16px;color:#333;height:50px;}
          td{height:40px;}
          tr{
            &:nth-child(2){
              td{
                color:#ec2e23;
                .list-num{display:inline-block;width:22px;height:22px;line-height:22px;border-radius:50%;font-size:16px;font-style:italic;color:#fff;background:#fa6143;text-align:left;text-indent: 4px;}
              }
            }
            &:nth-child(3){
              td{
                color:#ff9000;
                .list-num{display:inline-block;width:22px;height:22px;line-height:22px;border-radius:50%;font-size:16px;font-style:italic;color:#fff;background:#ff9000;text-align:left;text-indent: 4px;}
              }
            }
            &:nth-child(4){
              td{
                color:#3c84fc;
                .list-num{display:inline-block;width:22px;height:22px;line-height:22px;border-radius:50%;font-size:16px;font-style:italic;color:#fff;background:#3c84fc;text-align:left;text-indent: 4px;}
              }
            }
            &:nth-child(odd){
               td{background:#f6f6f6;}
             }
            &:hover{
              td{background:#e9f2fd;}
             }
          }
        }
      }
    }
    &.modle4{
      .ldchart2{height:620px;width:100%;}
    }
    &.modle5{
      .ldlist{
        padding:0 20px 0 38px;height:620px;
        li{
          list-style: disc;height:40px;line-height:40px;overflow:hidden;
          a{
            float:left;
            i{color:#e4332a;}
          }
          span{float:right;color:#999;}
        }
      }
      .ldlist-nodata{height:620px;line-height:620px;width:100%;font-size:20px;text-align:center;}
     }
  }

  @media screen and (min-width:1401px) and  (max-width: 1600px) {
    .ldmodle{
      &.modle1{
        .mokuai{
          .mokuai-t{
            font-size:16px;
            span{font-size:26px;}
          }
          .mokuai-b{
            p{
              &:last-child{
                font-size:12px;
                span{font-size:14px;}
              }
            }
          }
        }
      }
    }
  }
  @media screen and (max-width: 1400px) {
    .ldmodle{
      &.modle1{
        .mokuai{
          .mokuai-t{
            font-size:14px;
            span{font-size:20px;}
          }
          .mokuai-b{
            p{
              &:last-child{
                font-size:12px;
                span{font-size:12px;}
              }
            }
          }
        }
      }
    }
  }
  .font20{font-size:20px;color:#333 !important;}
</style>



