<template>
  <div class="con">
    <div class="hea_tit">
      <div class="ball">
        <span></span>
      </div>
      <span><router-link :to="{path:'/home/ResultQuery'}">{{listName}}</router-link></span>
      <button class="btn2" @click="download()"><i class="el-icon-download"></i><span>下载</span></button>
    </div>
    <div class="table-warp">
      <table>
        <tr class="thead">
          <td>序号</td>
          <td>考核部门</td>
          <td>是否上传</td>
          <td>上传时间</td>
          <td>考核数据</td>
          <td>信息事项数量</td>
          <td>信息数据数量</td>
          <td>有效信息项</td>
          <td>有效信息数量</td>
          <td>备注</td>
        </tr>
        <tr v-if="list.length > 0" v-for="(item,idx) in list">
          <td>{{idx+1}}</td>
          <td>{{item.ASSESS_DEPARTMENT}}</td>
          <td v-if="item.ISUPLOAD==0">否</td>
          <td v-if="item.ISUPLOAD==1">是</td>
          <td>{{item.UPLOAD_DATE}}</td>
          <td>{{item.ASSESS_DATA}}</td>
          <td>{{item.MATTER_COUNT}}</td>
          <td>{{item.DATA_COUNT}}</td>
          <td>{{item.VALID_MATTER_COUNT}}</td>
          <td>{{item.VALID_DATA_COUNT}}</td>
          <td>{{item.REMARK}}</td>
        </tr>
        <tr v-if="list.length === 0" class="noDataTr">
          <span>暂无数据</span>
        </tr>
      </table>
      <div class="pagination fr">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="10"
          layout="prev, pager, next, jumper"
          :total=this.count>
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script type="es6">
  import {puHttp} from '../../api/puHttp'
  export default {
    name: 'home',
    data(){
      return {
        currentPage:1,
        list:[],
        count:0,
        listName:''
      }
    },
    activated(){
      this.nameId = this.$route.query.id
      this.listName = this.$route.query.listName
      this.queryList();
    },
    methods: {
      handleCurrentChange(val) {
        this.currentPage = val;
        this.queryList();
      },
      queryList(){
        puHttp.resultList({
          page:this.currentPage,
          rows:10,
          taskId:this.nameId
        }).then((result)=>{
          this.list=result.data
          this.count= result.count
        }).catch((error) => {
          this.openObject("服务器连接超时,请稍后再试")
        });
      },
      //下载
      download(){
        puHttp.resultDownload({
          taskId:this.nameId
        }).then((result)=>{
          let base_url = window.BASE_URL;
          location.href=base_url+'/performance/downloadassess?taskId='+this.nameId
        }).catch((error) => {
          this.openObject("服务器连接超时,请稍后再试")
        })
      }
    },
  }
</script>

<style lang="scss" scoped>
  .con{
    width: 100%;
    /*height: 795px;*/
    background-color: #fff;
    min-height: calc(100vh - 120px);
    margin-top: 0 !important;
  }
</style>



