
let openObject = function(conObject) {
  this.$alert(conObject, '提示信息', {
    confirmButtonText: '确定',
  });
}
let dateObj = function (sj) {
  const d = new Date(sj)
  return d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate();
  //+ ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds()
}
global.callBackFn = null;
export  {
  openObject,
  dateObj
}



